import React, { useEffect } from "react";

import "./Contact.css";

import Navbar from "../../components/genral_components/Navbar/Navbar";

import ContactBanner from "../../components/contact-us-page-components/contact-us-banner/ContactBanner";

import ContactForm from "../../components/contact-us-page-components/contact-us-form/ContactForm";

import Carousel from "../../components/home_page_components/carousel/Carousel";

import Footer from "../../components/genral_components/Footer/Footer";

import { useLocation } from "react-router-dom";

export default function Contact(prop) {
  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className="contact-us-page-main-container">
      <Navbar />

      <ContactBanner />

      <ContactForm
        mainCategory={
          location.state && location.state.mainCategory
            ? location.state.mainCategory
            : null
        }
        subCategory={
          location.state && location.state.subCategory
            ? location.state.subCategory
            : null
        }
        price={
          location.state && location.state.price ? location.state.price : null
        }
      />

      <Carousel />

      <br />
      <br />
      <br />

      <Footer />
    </div>
  );
}
